              <path
                id="Forma_1"
                data-name="Forma 1"
                class="cls-1"
                d="M1568.54,437.155l-2.68-2.662a1.636,1.636,0,0,0-1.18-.493,1.6,1.6,0,0,0-1.18.493l-1.8,1.783a1.1,1.1,0,0,0-1.04.219l-0.77.767a1.046,1.046,0,0,0-.2,1.153l-8.65,8.617a0.153,0.153,0,0,0-.06.081v0.028c-0.03.028-.03,0.056-0.06,0.081h0l-1.54,4.667h0l-0.16.493-0.17.493c0,0.028,0,.056-0.03.056a0.931,0.931,0,0,0,.08.63,0.6,0.6,0,0,0,.09.137,0.876,0.876,0,0,0,.68.3h0.2c0.02,0,.05,0,0.05-0.028l0.69-.218,4.96-1.619h0a0.54,0.54,0,0,0,.2-0.109l8.68-8.642a0.748,0.748,0,0,0,.36.056,0.989,0.989,0,0,0,.71-0.3l0.78-.768a0.977,0.977,0,0,0,.21-1.069l1.8-1.783A1.632,1.632,0,0,0,1568.54,437.155Zm-12.87,13.774-1.41-1.4,8.24-8.231,1.41,1.4Zm6.12-10.344-8.25,8.23-1.4-1.425,8.24-8.2Zm-10.64,11.99-0.69-.686,1.18-3.567,1.55,1.537h0l1.54,1.537Zm14.05-10.288c-0.02-.028-0.02-0.056-0.05-0.056l-4.27-4.281a0.654,0.654,0,0,0-.14-0.109l0.6-.63,0.06,0.056h0l4.33,4.308a0.182,0.182,0,0,0,.08.056Zm2.62-3.458-1.73,1.728-3.61-3.62,1.73-1.728a0.691,0.691,0,0,1,.94,0l2.67,2.66A0.708,0.708,0,0,1,1567.82,438.829Z"
