                </ul>
              </template>
              <template v-else>
                <div
                  class="clip"
                  :content=" cellContent(field, item) "
                  v-tippy="{
																								placement : 'top',
																								animation : 'fade',
																								distance  : '-0',
																								offset    : '0,5',
																								delay     : [500,250],
