        @enter="enter"
        @leave="leave"
        @after-enter="afterEnter"
        @beforeLeave="beforeLeave"
        name="answer-transition"
      >
        <template v-for="childKey in itemChildren">
          <TreeElement
            :item="itemTree"
            :model_name="childKey"
            v-show="itemTree.show !== false"
            :depth="element_depth"
            :key="childKey+(itemTree.id ||Math.random())"
            :parent="item"
            :data-index="index"
