<script>
import TreeElement from "./TreeElement.vue";
import { EventBus } from "../main";
import Velocity from "velocity-animate";

export default {
  name: "TreeElement",
  data() {
    return {
      table_fields: this.$store.getters.getTableFields,
      animation: false
    };
  },
  mounted() {
    EventBus.$emit("treeMask", this.item);
    this.$set(this.item, "parent", this.parent);
    this.$set(this.item, "checked", false);
    this.imgtosvg();
  },

  methods: {
    goToCard() {
      if (this.editableFields.parameters["card_l"] && this.item.id)
        this.$bRouter.push(
          this.editableFields.parameters["card_url"] + "/" + this.item.id
        );
    },

    toggleLevel(element, e) {
      if (this.animation) return false;
      if (element.show === false) element.show = true;
      !element.hasOwnProperty("visible")
        ? this.$set(element, "visible", false)
        : false;
      element.visible = !element.visible;
    },

    beforeEnter(el) {
      this.animation = true;
      el.style.height = "0";
      el.style.opacity = 0;
    },

    enter(el, done) {
      Velocity(
        el,
        { opacity: 1, height: el.scrollHeight },
        {
          complete: element => {
            setTimeout(() => {
              el.style.height = "auto";
              setTimeout(() => {
                this.animation = false;
              }, 500);
              done();
            }, 250);
          }
        }
      );
    },

    afterEnter(el, done) {
      Velocity(el, { overflow: "visible", height: "auto" });
    },

    beforeLeave(el) {
      this.animation = true;
      el.style.height = `${el.scrollHeight}px`;
    },

    leave(el, done) {
      this.$nextTick(e => {
        Velocity(
          el,
          { opacity: 0, height: 0, overflow: "hidden" },
          {
            complete: () => {
              setTimeout(() => {
                this.animation = false;
              }, 500);
            }
          }
        );
      });
    },

    selectLevel() {
      let self = this;
      let formParameters = this.$store.getters.formParameters;
      let deepSelect =
        formParameters.select_in_deep === true &&
        formParameters.select_in_level !== true;
      let showChecked = formParameters.show_checked;
      let modeAdd = !this.item.checked;
      this.item.checked = !this.item.checked;
      send({ [this.model_name]: [this.item.id] });

      deepSelect
        ? deepUnder(this.itemChildren, this.item, this.depth)
        : deepLevel(this.itemChildren, this.item);

      function showCheckedItem(element) {
        !element.hasOwnProperty("visible")
          ? self.$set(element, "visible", false)
          : false;
        element.visible = true;
      }

      function getIDsAndSend(item, model) {
        showChecked ? showCheckedItem(item) : false;
        let itemsModel = item[model].map(e => {
          if (e.checked !== modeAdd) {
            e.checked = modeAdd;
            return e.id;
          }
        });
        itemsModel.length > 0 && typeof itemsModel[0] !== "undefined"
          ? send({ [model]: itemsModel })
          : false;
      }

      function deepUnder(itemChildren, item, level) {
        [...itemChildren].forEach(model => {
          let itemLocal;
          let modelExist = item.hasOwnProperty(model);
          if (!modelExist) return false;
          itemLocal = item[model];
          getIDsAndSend(item, model);
          [...itemLocal].forEach(itemL => {
            deepUnder(itemChildrens(level + 1, itemL), itemL, level + 1);
          });
        });
      }

      function deepLevel(itemChildren, item) {
        [...itemChildren].forEach(model => {
          let modelExist = item.hasOwnProperty(model);
          if (!modelExist) return false;
          getIDsAndSend(item, model);
        });
      }

      function itemChildrens(depth, item) {
        let modelsChildren = self.$store.getters.getDepthParameters(depth)[
          "children_models"
        ];
        let modelsChild = [];
        modelsChildren.forEach(model => {
          if (item.hasOwnProperty(model) && item[model].length > 0)
            modelsChild.push(model);
        });

        return modelsChild;
      }

      // Записать/Удалить IDs
      function send(data) {
        EventBus.$emit("treeSelectModel", { items: data, modeAdd });
      }
    },

    editLine(createNewElem) {
      let self = this;
      let res;
      let data = {
        modal: {
          link: this.$bRoute.meta.route,
          modal_title: "title",
          // fields: this.editableFields.columns,
          fields: this.editableFields.columns,
          type: "editRow",
          model: { ...this.item },
          model_name: this.model_name,
          addModels: [],
          send_request: false
        }
      };
      var link = this.item;
      if (createNewElem) {
        res = this.getNewItem();
        data.modal.fields = this.$store.getters.getFieldsParameters[
          res.child_model
        ]["columns"];
        let { parent, ...model } = res.empty_row;
        data.modal.model = model;
        data.modal.model_name = res.child_model;
        link = res.empty_row;
      }

      EventBus.$emit("showModal", data);
      EventBus.$on("editFinish", data => {
        console.log(data);
        for (var k in data) {
          link[k] = data[k];
        }
        if (createNewElem && data) {
          self.item[res.child_model].push(link);
        }
        EventBus.$off("editFinish");
      });
    },

    getNewItem() {
      let child_model = this.$store.getters.getDepthParameters(this.depth)[
        "children_models"
      ][0]; //todo добавление работает только для первой модели дочерних элементов
      let empty_row = JSON.parse(
        JSON.stringify(
          this.$store.getters.getFieldsParameters[child_model]["parameters"][
            "empty_row"
          ]
        )
      );
      if (!this.item[child_model]) this.item[child_model] = [];
      return { empty_row, child_model };
    },

    cellContent({ column_code }, item) {
      var elems = this.editableFields.columns.filter(
        el => el.column_code === column_code
      );
      return elems.length > 0 ? item[elems[0]["key"]] : null;
    },

    async actionHandler({ link, code, execute_fe_action_l }, items) {
      try {
        // !Проверка на надобность запроса
        if (execute_fe_action_l) {
          switch (code) {
            case "edit":
              this.goToCard();
              break;
            case "show":
              break;
          }
          return false;
        }
        let data = {
          [this.model_name]: items
        };
        let res = await this.$http.post(link, data);
        if (res.data.requery) {
          EventBus.$emit("refreshTreeQuery");
        }
        await this.$store.dispatch("showToast", {
          template: res.data.template,
          title: res.data.title,
          message: res.data.message,
          position: res.data.position
        });
      } catch (error) {
        if (error.response.data.requery) {
          EventBus.$emit("refreshTreeQuery");
        }
        await this.$store.dispatch("showToast", {
          template: "error",
          title: error.response.status,
          message: error.response.data.message,
          position: error.response.data.position
        });
      }
    }
  },
  computed: {
    getIcon() {
      let icon,
        depthParam = this.$store.getters.getDepthParameters(this.depth);
      if (!depthParam.use_icon) return false;
      icon = this.editableFields.parameters.icon
        ? this.editableFields.parameters.icon
        : depthParam.icon;
      return icon;
    },
    getActions() {
      let actionsList;
      this.editableFields.columns.forEach(e => {
        if (e.key === "actions") {
          actionsList = e.actions_list;
        }
      });
      return actionsList;
    },
    element_depth() {
      return this.depth + 1;
    },
    title() {
      let title = this.editableFields.parameters["model_title_field"];
      return this.item[title];
    },
    itemChildren() {
      let modelsChildren = this.$store.getters.getDepthParameters(this.depth)[
        "children_models"
      ];
      let modelsChild = [];
      modelsChildren.forEach(model => {
        if (this.item.hasOwnProperty(model) && this.item[model].length > 0)
          modelsChild.push(model);
      });

      return modelsChild;
    },
    editableFields() {
      return this.$store.getters.getFieldsParameters[this.model_name];
    }
  },

  props: {
    item: {
      type: Object
    },
    dragDrop: {
      type: Object
    },
    depth: {
      type: Number
    },
    model_name: {
      type: String
    },
    parent: {
      type: Object
    }
  },

  components: {
    TreeElement
  }
};
</script>

