<template>
  <div class="tree container">
    <transition name="fade" mode="out-in">
      <!--Анимация-->
      <div v-if="loading" key="spinner" class="loading-container">
        <atom-spinner :animation-duration="1000" color="var(--accent)" />
      </div>
      <div key="content" v-if="!loading">
        <h1>{{form_parameters.form_title}}</h1>
        <div class="row top-action">
          <div class="col d-flex">
            <template v-if="sets && sets.list_top_dropdown_actions.length > 2">
              <div class="dropdown">
                <button class="btn btn-dropdown" :disabled="!isChecked">
                  Действия
                  <img class="drop-i" src="/img/dropdown-i.png" alt />
                </button>
                <div class="dropdown_box">
                  <ul>
                    <li v-for="item in sets.list_top_dropdown_actions">
                      <button
                        :content="item.title"
                        v-tippy="{
																														placement : 'left',
																														animation : 'fade',
																														distance  : '-0',
																														offset    : '0,5',
																														delay     : [500,250],
																														arrow     : false,
																														theme     : 'info'
																											 }"
                        @click="massAction(item.code, item.link)"
                        class="dropwond_link"
                      >{{item.title}}</button>
                    </li>
                  </ul>
                </div>
              </div>
            </template>

            <div
              class="btn btn-green btn-icon"
              @click="dragAndDropInit('.dragAndDropLine','ul',true,'li')"
              v-if="form_parameters.drag_and_drop"
              :class="{ active: dragDropEnable,'not-active': !dragDropEnable }"
            >
              <img class="svg" src="/img/interfacedashboard/dragdrop.svg" alt="arrow" />
            </div>

            <div class="btn btn-green btn-small" @click="addFirstLevelRow">Добавить</div>
          </div>
          <div class="col-md-7 d-flex align-items-center">
            <transition name="fade">
              <p class="search_result" v-if="filterActive">
                <b class="accent">Результат поиска:</b>
                {{resultItems.length}} объекта
              </p>
            </transition>
            <div class="search-box">
              <input
                type="search"
                class="search"
                placeholder="Поиск"
                @input="searchTree"
                v-model="filter"
              />
              <button class="search_btn" :class="{active: filter.length > 0}">
                <img src="/img/interfacedashboard/Search_icon.svg" class="svg" alt />
              </button>
            </div>
          </div>
        </div>

        <div class="row">
          <div class="col">
            <div :class="{dragEnable: this.dragDropEnable}" class="tree-box">
              <div class="table-header">
                <div class="checkbox-wrapper"></div>
                <div class="left-part"></div>
                <div class="right-part">
                  <table>
                    <tr>
                      <td v-for="field in table_fields" :key="field.key">{{ field.label}}</td>
                    </tr>
                  </table>
                </div>
              </div>
              <transition-group tag="ul" name="answer-transition">
                <TreeElement
                  v-for="(item,index) in items"
                  :model_name="form_parameters.form_base_data_model"
                  :key="item.id ||Math.random()"
                  v-if="!filterActive ? true : Boolean(item.show)"
                  :item="item"
                  :depth="1"
                  :dragDrop="dragDrop"
                  :parent="null"
                ></TreeElement>
              </transition-group>
            </div>
          </div>
        </div>
        <div class="row action-buttons">
          <div
            class="col d-flex justify-content-end page_buttons_bottom margin-top-2"
            v-if="sets && sets.card_actions"
          >
            <a
              v-for="action in sets.card_actions"
              :id="action.code"
              :key="action.title"
              :class="action.class"
              @click="saveModel(action)"
            >{{action.title}}</a>
          </div>
        </div>
      </div>
    </transition>
  </div>
</template>

<script>
import vSelect from "vue-select";
import { AtomSpinner } from "epic-spinners";
import TreeElement from "./TreeElement";
import { EventBus } from "../main";
import debounce from "lodash.debounce";
import { dragAndDrop } from "../mixins/dragAndDrop";

export default {
  name: "Tree",

  mixins: [dragAndDrop],

  data() {
    return {
      sets: {},
      items: [],
      treeMask: [],
      resultItems: [],
      filter: "",
      filterActive: false,
      increment: 0,
      table_fields: {},

      form_parameters: {},

      main_data_models: {},
      fields_parameters: {},
      loading: true,
      checkedItems: {}
    };
  },

  props: {
    treeProp: {
      type: Object,
      default: null
    }
  },

  methods: {
    searchTree: debounce(function() {
      let self = this;
      let subStringFind = toString(this.filter);
      resetBacklight(this.resultItems);
      resetShowFound(this.resultItems);
      if (subStringFind.length === 0) {
        this.filterActive = false;
        return false;
      }
      this.filterActive = true;

      let resultItems = filterList.call(this);
      backlight(resultItems);
      showFound.call(this, resultItems);
      this.resultItems = resultItems;

      // Перевод значение в строку
      function toString(string) {
        if (string) {
          return string.toString().toLocaleLowerCase();
        } else {
          return "";
        }
      }

      // Открыть всех родителей
      function showParent(item) {
        self.$set(item, "visible", true);
        self.$set(item, "show", true);
        if (item.parent) showParent(item.parent);
      }

      // Спрятать всех родитилей
      function hideParent(item) {
        item.visible = false;
        item.show = false;
        item.parent ? hideParent(item.parent) : false;
      }

      // Показать найденные
      function showFound(resultItems) {
        resultItems.forEach(item => {
          item.parent ? showParent(item.parent) : false;
        });
      }

      // Сбросить найденные
      function resetShowFound(items) {
        items.forEach(item => {
          item.parent ? hideParent(item.parent) : false;
        });
      }

      // Поиск подстроки в одномерном массиве
      function filterList() {
        return this.treeMask.filter(item => {
          let values = Object.values(item);
          return values.some(
            val => toString(val).indexOf(subStringFind) !== -1
          );
        });
      }

      // Сбросить подстветку
      function resetBacklight(items) {
        items.forEach(e => (e.backlight = false));
      }

      // Установить подстветку
      function backlight(items) {
        items.forEach(e => self.$set(e, "backlight", true));
      }
    }, 1000),
    async componentData(Request, params = null) {
      try {
        let res = {};
        if (!this.treeProp) res = await this.$http.post(Request, params);
        else res.data = this.treeProp;

        this.form_parameters = res.data.form_parameters;

        this.$store.commit("formParameters", this.form_parameters);

        this.main_data_models = res.data.main_data_models;
        this.sets = res.data.sets;
        this.table_fields = res.data.table_fields;
        this.fields_parameters = res.data.fields_parameters;

        this.items = this.main_data_models[
          this.form_parameters.form_base_data_model
        ];

        this.$store.commit("setFormParameters", {
          table_fields: res.data.table_fields,
          fields_parameters: this.fields_parameters,
          depths_parameters: res.data.depths_parameters
        });

        this.$nextTick(e => {
          this.loading = false;
        });
      } catch (error) {
        console.log(error);
        this.$store.dispatch("showToast", {
          template: "error",
          title: error.response.status,
          message: error.response.data.message,
          position: error.response.data.position
        });
        this.$bRouter.push({ path: "/" + error.response.status });
      }
    },
    /**
     * Реагирует на нажатие кнопок внизу страницы
     * @param {object} action - обьект, описывающий действия. Пробрасывается из сета возможных действий
     */
    async saveModel(action) {
      this.loading = true;
      try {
        if (action.code == "back") {
          if (this.model_has_changed) {
            this.$toast.error("Изменения не были сохранены", "", {
              position: "topRight"
            });
            this.$bRouter.go(-1);
            return;
          } else {
            this.$bRouter.go(-1);
            return;
          }
        }

        this.treeMask.forEach(e => (e.parent = null));

        let data = {
          main_data_models: this.main_data_models,
          form_parameters: {
            form_base_data_model: this.form_parameters.form_base_data_model
          }
        };
        let res = await this.$http.post(action.link, data);
        this.treeMask = [];
        this.main_data_models = { ...res.data.main_data_models };
        this.items =
          res.data.main_data_models[
            res.data.form_parameters.form_base_data_model
          ];
        this.$store.dispatch("showToast", {
          template: res.data.template ? res.data.template : "success",
          title: res.data.title,
          message: res.data.message,
          position: res.data.position
        });

        // if (action.code === 'ok') this.$bRouter.go(-1);
      } catch (error) {
        this.$store.dispatch("showToast", {
          template: "error",
          title: error.response.status,
          message: error.response.data.message,
          position: error.response.data.position
        });
        if (error.response.data.requery) {
          this.$nextTick(async () => {
            this.paramCard.id = this.step_l
              ? this.row_id_fe_route_step_object
              : this.$bRoute.params[this.$bRoute.meta.id_field];
            await this.componentData(this.$bRoute.meta.route, this.paramCard);
          });
        }
        if (error.response.status !== 400)
          this.$bRouter.replace({ path: "/" + error.response.status });
      }
      setTimeout(e => {
        this.loading = false;
      }, 500);
    },
    /**
     * Массовые действие
     * @param {string} code
     * @param {string} url
     */
    massAction(code, url) {
      this.$http
        .post(url, this.checkedItems)
        .then(async res => {
          if (code === "download") {
            await this.saveFile(res.data);
            return;
          }

          //обновление элементов списка после действия
          var data = {
            id: this.$bRoute.params[this.$bRoute.meta.id_field]
          };
          if (this.$bRoute.meta.prev_controller_alias) {
            data["dependent"] = {
              controller_alias: this.$bRoute.meta.prev_controller_alias,
              id: this.$bRoute.params[this.$bRoute.meta.prev_id_field]
            };
          }
          this.componentData(this.$bRoute.meta.route, data);
          this.$store.dispatch("showToast", {
            template: res.data.template,
            title: res.data.title,
            message: res.data.message,
            position: res.data.position
          });
        })
        .catch(error => {
          this.$store.dispatch("showToast", {
            template: "error",
            title: error.response.status,
            message: error.response.data.message,
            position: error.response.data.position
          });
        });
    },

    addFirstLevelRow() {
      let first_level_model_name = this.form_parameters["form_base_data_model"];
      let { parameters, columns } = this.fields_parameters[
        first_level_model_name
      ];
      let empty_row = JSON.parse(JSON.stringify(parameters.empty_row));
      let data = {
        modal: {
          link: this.$bRoute.meta.route,
          modal_title: "title",
          // fields: this.editableFields.columns,
          fields: columns,
          type: "editRow",
          model: empty_row,
          model_name: first_level_model_name,
          addModels: [],
          send_request: false
        }
      };
      var link = empty_row;
      EventBus.$emit("showModal", data);
      EventBus.$on("editFinish", data => {
        console.log(data);
        if (data) {
          for (var k in data) {
            link[k] = data[k];
          }
          this.items.push(link);
        }
        EventBus.$off("editFinish");
      });
    }
  },

  created() {
    EventBus.$on("treeMask", payload => {
      this.treeMask.push(payload);
    });
    EventBus.$on("refreshTreeQuery", payload => {
      this.componentData(this.$bRoute.meta.route, {
        id: this.$bRoute.params[this.$bRoute.meta["id_field"]]
      });
    });

    let wrapperPage = document.querySelector(".wrapper");
    wrapperPage.style.overflow = "hidden";

    EventBus.$on("treeSelectModel", payload => {
      let modeAdd = payload.modeAdd;
      let keys = Object.keys(payload.items);

      // Проверка на наличие модели в Объекте выделеных строк
      keys.forEach(e => {
        this.checkedItems.hasOwnProperty(e)
          ? splitValues(this.checkedItems, e, payload.items[e])
          : this.$set(this.checkedItems, e, payload.items[e]);
      });

      // Если модель пустая, то удалить её из объекта
      function checkEmptyModel(object, key) {
        let length = object[key].length;
        length === 0 ? delete object[key] : false;
      }

      // Удаление/Добавление в массив значение
      function splitValues(object, key, values) {
        values.forEach(val => {
          let includeValue = object[key].includes(val);
          if (!includeValue && modeAdd) {
            object[key].push(val);
          } else if (includeValue && !modeAdd) {
            let index = object[key].indexOf(val);
            object[key].splice(index, 1);
            checkEmptyModel(object, key);
          }
        });
      }
    });
  },

  computed: {
    isChecked() {
      let length = 0;
      [...Object.keys(this.checkedItems)].forEach(key => {
        length += this.checkedItems[key].length;
      });
      return length !== 0;
    }
  },

  components: {
    vSelect,
    TreeElement,
    AtomSpinner
  },
  beforeDestroy() {
    let wrapperPage = document.querySelector(".wrapper");
    wrapperPage.style.overflow = "visible";
  },
  mounted() {
    this.componentData(this.$bRoute.meta.route, {
      id: this.$bRoute.params[this.$bRoute.meta["id_field"]]
    });
    this.$nextTick(e => {
      this.imgtosvg();
    });
  }
};
</script>

